turn this.mainStore.getAccounts.length > 1\n\t\t},\n\t},\n\tmethods: {\n\t\tonAccountCreated() {\n\t\t\tlogger.info('account successfully created, redirecting …')\n\t\t\tthis.$router.push({\n\t\t\t\tname: 'home',\n\t\t\t})\n\t\t},\n\t},\n}\n</script>\n\n<style>\n.container{\n display: flex;\n justify-content: center;\n}\n</style>\n"],"sourceRoot":""}]);
